import debounce from "lodash/debounce";
export const tableMixin = {
  data: () => ({
    dialog: false,
    dialogDelete: false,
    dialogKey: 0,
    items: [],
    options: {},
    editedItem: {},
    editedIndex: -1,
    defaultItem: {},
  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1
        ? "New " + this.itemName
        : "Edit " + this.itemName;
    },
  },

  watch: {
    dialog(val) {
      this.dialogKey++;
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  methods: {
    editItem(item) {
      this.dialogKey++, (this.editedIndex = this.items.indexOf(item));
      this.editedItem = { ...item };
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.items.indexOf(item);
      this.editedItem = { ...item };
      this.dialogDelete = true;
    },

    close() {
      this.dialog = false;
      this.dialogKey++,
        this.$nextTick(() => {
          this.editedIndex = -1;
          this.editedItem = { ...this.defaultItem };
        });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = { ...this.defaultItem };
        this.editedIndex = -1;
      });
    },

    deleteItemConfirm() {
      this.$axios
        .post("secured/delete" + this.itemName, this.editedItem)
        .then(() => {
          this.$toast.setMessage("Successfully Deleted", 2);
          this.closeDelete();
          this.fetchItems();
        })
        .catch(({ response }) => {
          this.$toast.setMessage(response.data, 1);
        });
    },
    save() {
      this.$refs.customForm.validate().then((success) => {
        if (!success) {
          return;
        }
        this.$axios
          .post("secured/save" + this.itemName, this.editedItem)
          .then(() => {
            this.$toast.setMessage("Successfully Saved", 2);

            this.close();
            this.fetchItems();
          })
          .catch(({ response }) => {
            this.$toast.setMessage(response.data, 1);
          });
      });
    },
    fetchItems() {
      this.$axios
        .post("secured/get" + this.itemName + "List")
        .then(({ data }) => {
          this.items = data;
        })
        .catch(({ response }) => {
          this.$toast.setMessage(response.data, 1);
        });
    },
  },
  created() {
    this.fetchItems();
  },
};
